<template>
    <div>
        <Toolbar>
            <template v-slot:title>
              All users
            </template>
            <template v-slot:prepend>
                <v-btn icon @click="$router.go(-1)">
                    <v-icon>mdi-arrow-left</v-icon>
                </v-btn>
            </template>
            <template v-slot:append>
                <v-spacer></v-spacer>
                <v-btn icon @click="">
                    <v-icon>mdi-magnify</v-icon>
                </v-btn>
            </template>
        </Toolbar>
        <v-list v-if="users && users.length" class="pt-0">
          <template v-for="(item, index) in users">
            <v-divider v-if="index != 0" :key="index" inset></v-divider>
            <v-list-item
              class="pr-1"
              :key="item.id"
            >
              <v-list-item-avatar>
                <v-icon large>mdi-account-circle</v-icon>
              </v-list-item-avatar>

              <v-list-item-content>
                <v-list-item-title v-text="item.username"></v-list-item-title>
              </v-list-item-content>
              <v-list-item-action>
                  <v-btn small text v-if="!item.friendship" @click.stop="addFriend(item.id)">
                      <v-icon left>mdi-account-plus</v-icon>
                      Add to friends
                  </v-btn>
                  <v-hover v-else v-slot:default="{ hover }">
                    <v-btn v-if="hover" small text @click.stop="deleteFriend(item.id)">
                      delete
                      <v-icon right color="error">mdi-trash-can-outline</v-icon>
                    </v-btn>
                    <v-btn v-else small text>
                      friend
                      <v-icon right color="success">mdi-check-bold</v-icon>
                    </v-btn>
                  </v-hover>
              </v-list-item-action>
            </v-list-item>
          </template>
          <v-divider inset></v-divider>
        </v-list>
        <template v-else>
            <p class="text-center pt-4 subtitle-1">No other users</p>
        </template>
    </div>
</template>

<script>
import Toolbar from '@/components/Toolbar'

export default {
  name: 'Users',
  components: {
    Toolbar
  },
  data () {
    return {
    }
  },
  computed: {
    users () {
      return this.$store.getters.usersWithFriendship
    }
  },
  methods: {
    addFriend (id) {
      this.$store.dispatch('addFriend', id).then(resp => {
        this.users.forEach(user => {
          if (user.id === id) {
            user.friendship = true
          }
        })
        console.log(resp)
        this.$forceUpdate()
      }).catch(err => {
        console.log(err)
      })
    },
    deleteFriend (id) {
      this.$store.dispatch('deleteFriend', id).then(resp => {
        this.users.forEach(user => {
          if (user.id === id) {
            user.friendship = false
          }
        })
        console.log(resp)
        this.$forceUpdate()
      }).catch(err => {
        console.log(err)
      })
    }
  },
  mounted () {
    this.$store.dispatch('getUsers')
    this.$store.dispatch('getFriends')
  }
}
</script>